.fenxi {
  width: 411px;
  height: 280px;
}
.fenxi .tiele {
  width: 137px;
  height: 37px;
  line-height: 37px;
  text-align: center;
  font-size: 16px;
  letter-spacing: 1px;
  color: #ffffff;
}
.fenxi .kuang {
  margin-top: 10px;
  width: 411px;
  height: 233px;
  overflow: hidden;
}
.container {
  overflow: hidden;
  width: 380px;
  top: 26px;
  position: relative;
  left: 17px;
  height: 180px;
  margin-top: 15px;
}
.labeltextleft {
  position: absolute;
  width: 100px;
  color: #C6E5FF;
  left: 230px;
  top: 155px;
  font-size: 14px;
  fill: #C6E5FF;
}
.labeltextright {
  position: absolute;
  width: 100px;
  color: #C6E5FF;
  right: 155px;
  top: 165px;
  font-size: 14px;
  fill: #C6E5FF;
}
.righttbox {
  position: relative;
  top: -232px;
  left: 210px;
  width: 160px;
  height: 90px;
  z-index: 99999;
}
.righttbox .xian {
  float: left;
  display: inline-block;
  width: 40px;
  height: 90px;
}
.righttbox .text {
  float: left;
  margin-left: 10px;
  margin-top: 15px;
  display: inline-block;
  width: 101px;
  height: 90px;
}
.righttbox .text span {
  width: 101px;
  height: 20px;
  font-size: 14px;
  text-align: center;
  line-height: 20px;
  letter-spacing: 1px;
  color: #ffffff;
  background-image: linear-gradient(0deg, #2668dc 0%, #7979f4 100%), linear-gradient(#0090ff, #0090ff);
  background-blend-mode: normal, 
        normal;
  border-radius: 6px;
}
.righttbox .text p {
  font-size: 16px;
  letter-spacing: 1px;
  color: #ffffff;
  font-weight: 700;
}
.righttbox .text p i {
  font-style: normal;
  font-size: 12px;
  letter-spacing: 1px;
  color: #ffffff;
  font-weight: 400;
}
.leftbox {
  position: relative;
  top: -315px;
  left: 45px;
  width: 160px;
  height: 72px;
  z-index: 99999;
}
.leftbox .xian {
  float: left;
  display: inline-block;
  width: 40px;
  height: 70px;
}
.leftbox .text {
  float: left;
  margin-left: 10px;
  margin-top: 15px;
